import React, { useCallback, useEffect } from 'react';
import { navigate } from 'gatsby';
import Page from 'components/Layout/Page';
import { Container, Grid, Typography, Snackbar, Box } from '@mui/material';
import MentoringApplyForm from 'components/Forms/MentoringApply';
import { MentoringApplyFormDto } from 'src/models/forms';
import { applyMentoringForm } from 'remotes/lubycon';
import { MENTORING_GROUP, MENTORING_EXPIRY } from 'src/constants';
import * as Sentry from '@sentry/react';
import Countdown from 'components/Utils/CountDown';
import { useCountDown } from 'hooks/useCountDown';
import { logger } from '@lubycon/logger';
import { useBooleanState } from '@lubycon/react';

const mentoringApplyPageLogger = logger.getPageLogger('mentoring_project_apply_page');

const MentoringApply = () => {
  const [isOpenErrorSnackbar, openErrorSnackbar, closeErrorSnackbar] = useBooleanState();
  const [loading, startLoading, endLoading] = useBooleanState();
  const { expired } = useCountDown(MENTORING_EXPIRY);
  const handleSubmit = useCallback(
    async (data: MentoringApplyFormDto) => {
      if (expired) {
        return;
      }

      startLoading();
      mentoringApplyPageLogger.click('mentoring_project_form_apply', {
        group: MENTORING_GROUP,
        position: data.position,
        careerYear: data.careerYear,
      });
      try {
        await applyMentoringForm(data);
        navigate('/mentoring/apply-result');
      } catch (e) {
        openErrorSnackbar();
        Sentry.captureException(e, {
          user: {
            email: data.email,
            username: data.name,
          },
        });
      } finally {
        endLoading();
      }
    },
    [expired, startLoading, endLoading, openErrorSnackbar]
  );

  const title = '  ';
  const description = `    !`;

  const handleSnackbarClose = useCallback(() => closeErrorSnackbar(), [closeErrorSnackbar]);

  useEffect(() => {
    mentoringApplyPageLogger.view();
  }, []);

  return (
    <Page pageName={title} showTitle={false} description={<Typography component="span">{description}</Typography>}>
      <Container>
        <Grid container justifyContent="center" sx={{ marginTop: 15, marginBottom: 4 }}>
          <Grid item xs={12} md={8}>
            <Typography
              variant="h4"
              component="h1"
              gutterBottom={true}
              sx={{ fontWeight: 'bold', textAlign: 'center' }}
            >
              {title}
            </Typography>
            <Box sx={{ textAlign: 'center' }}>
              <Countdown expiry={MENTORING_EXPIRY} expiredText="" />
            </Box>
          </Grid>
        </Grid>
        <Grid container justifyContent="center">
          <Grid item xs={12} md={8}>
            <MentoringApplyForm loading={loading} onSubmit={handleSubmit} />
          </Grid>
        </Grid>
      </Container>
      <Snackbar
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'center',
        }}
        open={isOpenErrorSnackbar}
        autoHideDuration={5000}
        onClose={handleSnackbarClose}
        message="     !    help@lubycon.io "
      />
    </Page>
  );
};

export default MentoringApply;
